html {
  font-family: Roboto, Helvetica, Arial, sans-serif;
}
body {
  padding: 24px;
  padding-top: 0;
}
h1,
.h1 {
  font-weight: 300;
  border-bottom: solid 1px #dddddd;
  padding-bottom: 16px;
  color: #333333;
}
h2,
.h2 {
  font-weight: 300;
  font-size: 24px;
  margin-bottom: 16px;
}
h3,
.h3 {
  font-weight: 300;
  font-size: 16px;
  margin-bottom: 16px;
}
.dna-swatch {
  font-family: Roboto, Helvetica, Arial, sans-serif;
  font-size: 14px;
  display: block;
  width: 400px;
  height: 50px;
  color: #ffffff;
}
.dna-swatch-label {
  display: block;
  float: left;
  width: 400px;
  padding: 16px;
}
.dna-swatch-label:before,
.dna-swatch-label:after {
  content: '';
  display: table;
}
.dna-swatch-label:after {
  clear: both;
}
.dna-swatch-label-name {
  display: block;
  width: 184px;
  float: left;
}
.dna-swatch-label-code {
  display: block;
  width: 184px;
  font-family: Courier New, Courier, monospace;
  float: right;
  text-align: right;
}
.dna-swatch-list {
  margin-bottom: 20px;
}
.icon-matrix {
  margin-bottom: 16px;
  border: solid 1px #dddddd;
  background-color: #efefef;
  padding: 8px;
}
.icon-matrix:before,
.icon-matrix:after {
  content: '';
  display: table;
}
.icon-matrix:after {
  clear: both;
}
.icon-matrix > li {
  display: block;
  float: left;
  text-align: center;
  padding: 8px;
}
.icon-matrix.icon-icon > li {
  width: 140px;
  height: 140px;
}
.icon-matrix.icon-picto > li {
  width: 160px;
  height: 160px;
}
.icon-matrix.icon-ill > li {
  width: 240px;
  height: 240px;
}
